import ErmjBaseHandler from "./ErmjBaseHandler";

export default class ErmjDealHandler extends ErmjBaseHandler{
    protected execute(msg){
        let bankerSeat = this.context.get(this.Define.FieldBankerSeat);
        let userArr = msg._para.user_cards || [];

        let map = {};
        userArr.forEach(data => {
            let localSeat = this.SitHelper.serverSToLocalN(data.chair);
            map[localSeat] = data;
        });
        this.mainUI.onDeal(map, bankerSeat, msg._para.left_count);
    }

    protected executeSync(msg){
        let userArr = msg._para.user_cards || [];
        
        let map = {};
        userArr.forEach(data => {
            let localSeat = this.SitHelper.serverSToLocalN(data.chair);
            map[localSeat] = data;
        });
        this.mainUI.onDealDirectly(map, msg._para.left_count);
    }
}